form {
  display: block;
  position: relative;
  min-height: 7.5rem;
}

#btn {
  float: left;
  width: 1.5rem;
  height: 1.5rem;
  background: transparent;
  color: lightgray;
  cursor: pointer;
}
#btn svg {
  fill: currentColor;
  display: inherit;
  width: 1rem;
  height: 1rem;
  margin-left: 0.2rem;
  transition: color 0.5s ease-in, transform 0.5s linear;
  -webkit-transition: color 0.5s ease-in, transform 0.5s linear;
  transform: rotate(0) translateZ(0);
  backface-visibility: hidden;
  will-change: transform;
}
#btn:hover {
  color: #b9b9b9;
}
#btn.active svg {
  color: #6f6fd1;
  transform: rotate(720deg) translateZ(0);
}

#input-box {
  max-height: 1.5rem;
  height: 1.5rem;
  cursor: text;
  position: relative;
  overflow: hidden;
  padding: 0 0.5rem;
  line-height: 1.5rem;
  background-color: #f9f9f9;
  font-weight: 400;
}
#input-box #input-box-field {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0.5rem;
  right: 0.5rem;
  white-space: nowrap;
  overflow-x: scroll;
  min-height: 2.5rem;
  resize: none;
  text-overflow: ellipsis;
  user-select: text;
  -webkit-touch-callout: text;
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  -o-user-select: text;
}
#input-box #input-box-field:focus {
  outline: 0;
}
#input-box #input-box-field:not(:empty) ~ #input-box-placeholder {
  display: none;
}
#input-box #input-box-field:empty {
  caret-color: lightgray;
}
#input-box #input-box-field br, #input-box #input-box-field p, #input-box #input-box-field div {
  display: none;
}
#input-box #input-box-field .emoji {
  cursor: text;
  vertical-align: middle;
  display: inline;
  max-width: 24px;
  min-width: 24px;
  max-height: 24px;
  min-height: 24px;
}
#input-box #input-box-placeholder {
  position: absolute;
  top: 0;
  left: 0.5rem;
  right: 0.5rem;
  pointer-events: none;
  color: lightgray;
}
#input-box #input-box-placeholder::before {
  content: attr(data-ph);
}

#emoji-box {
  width: 100%;
  margin-top: 0.5rem;
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
  -webkit-transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
  will-change: opacity, visibility;
}
#emoji-box.show {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
#emoji-box::before {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0.5rem 0.75rem 0.5rem;
  border-color: transparent transparent #f9f9f9 transparent;
  content: "";
  top: -0.45rem;
  left: 0.25rem;
}
#emoji-box div {
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  background-color: #f9f9f9;
  padding: 0 0.75rem;
  width: 100%;
  height: 4.5rem;
  overflow-y: scroll;
  line-height: 0.25rem;
}
#emoji-box div::before, #emoji-box div::after {
  height: 0.75rem;
  left: 0;
  right: 0;
  top: 0;
  content: "";
  position: sticky;
  position: -webkit-sticky;
  display: inherit;
  background: linear-gradient(to bottom, #f9f9f9, transparent);
  z-index: 2;
}
#emoji-box div::after {
  top: auto;
  bottom: -1px;
  background: linear-gradient(to bottom, transparent, #f9f9f9);
}
#emoji-box div::-webkit-scrollbar {
  background: transparent;
  width: 0.5rem;
}
#emoji-box div::-webkit-scrollbar-button {
  display: none;
}
#emoji-box div::-webkit-scrollbar-track-piece {
  background-color: #ececec;
}
#emoji-box div::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  transition: background-color 0.1s linear;
  -webkit-transition: background-color 0.15s ease-in-out;
}
#emoji-box div::-webkit-scrollbar-thumb:active {
  background-color: #6f6fd1;
}
#emoji-box div .emoji {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  background-clip: content-box;
  transition: opacity 0.1s linear, background-color 0.1s ease-in, border-color 0.1s ease-in;
  cursor: pointer;
  opacity: 0.9;
}
#emoji-box div .emoji:hover {
  opacity: 1;
  background-color: #e0e0e0;
  border-color: #e0e0e0;
}

.emoji {
  display: inline-block;
}

